import React from 'react';
import "./propertyList.css";
import { Link } from 'react-router-dom';

export default function PropertyList() {
  const items = [
    { id: 1, type: 'Hotels', count: '233 Hotel', imageUrl: 'https://r-xx.bstatic.com/xdata/images/xphoto/263x210/57584488.jpeg?k=d8d4706fc72ee789d870eb6b05c0e546fd4ad85d72a3af3e30fb80ca72f0ba57&o=' },
    { id: 2, type: 'Apartments', count: '500 Apartment', imageUrl: 'https://q-xx.bstatic.com/xdata/images/hotel/263x210/119467716.jpeg?k=f3c2c6271ab71513e044e48dfde378fcd6bb80cb893e39b9b78b33a60c0131c9&o=' },
    { id: 3, type: 'Resorts', count: '9542 Resorts', imageUrl: 'https://r-xx.bstatic.com/xdata/images/hotel/263x210/52979454.jpeg?k=6ac6d0afd28e4ce00a8f817cc3045039e064469a3f9a88059706c0b45adf2e7d&o=' },
    { id: 4, type: 'Villas', count: '7445 Villas', imageUrl: 'https://q-xx.bstatic.com/xdata/images/hotel/263x210/100235855.jpeg?k=5b6e6cff16cfd290e953768d63ee15f633b56348238a705c45759aa3a81ba82b&o=' },
    { id: 5, type: 'Cabins', count: '2330 Cabins', imageUrl: 'https://r-xx.bstatic.com/xdata/images/xphoto/263x210/45450084.jpeg?k=f8c2954e867a1dd4b479909c49528531dcfb676d8fbc0d60f51d7b51bb32d1d9&o=' },
  ];

  return (
    <div className='pList'>
      {items.map((item) => (
        <Link className='a' to={`/hotels/${item.id}`} key={item.id}>
          <div className="pListItem">
            <img className='pListImg' src={item.imageUrl} alt="" />
            <div className="pListTitles">
              <h1>{item.type}</h1>
              <h2>{item.count}</h2>
            </div>
          </div>
        </Link>
      ))}
    </div>
  );
}
